<template>
  <body>
  <nav class="navbar navbar-light">
    <div class="container d-flex justify-content-center"><a class="navbar-brand text-primary" href="#">
      <i class="fa d-inline fa-lg fa-circle-o"></i>
    </a></div>
  </nav>
  <div class="py-5 bg-light">
    <div class="container">
      <div class="row">
        <div class="p-5 col-md-7 d-flex flex-column justify-content-center">
          <h3 class="display-4 mb-3">Heaven and earth</h3>
          <p class="mb-4 lead">"Live Free or Die"&nbsp;</p>
        </div>
        <div class="p-5 col-md-5">
          <h3 class="mb-3">Welcome</h3>
          <form>
            <div class="form-group"><label>Name</label> <input class="form-control" v-model="loginForm.username"
                                                               placeholder="Type here">
              <div class="form-group"><label>Password</label><input class="form-control" v-model="loginForm.password"
                                                                    placeholder="Type here"></div>
            </div>
            <button class="btn mt-4 btn-block btn-outline-dark p-2" v-on:click="login"><b>Login</b></button>
          </form>
        </div>
      </div>
    </div>
  </div>
  </body>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      responseResult: []
    }
  },
  methods: {
    login () {
      this.$axios
        .post('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        .then(successResponse => {
          if (successResponse.data.code === 200) {
            this.$router.replace({path: '/index'})
          }
        }, failureResponse => {
          if (failureResponse.data.code === 400) {
            this.$router.replace({path: '/loginFailed'})
          }
        })
        .catch(error => console.log(error))
    }
  }
}
</script>

